let userId = '',
    password = '';
import LongButton from '../../components/my-components/LongButton';
import PopUp from '../../components/my-components/PopUp2.vue';
let type = '';
let session_id = ''; //
let questions = []; //	安全问题
let registerType = ''; //	注册类型(username,mobile,email)
export default {
    components: {
        LongButton,
        PopUp
    },
    computed: {
        canSub() {
            return this.newName === '' || this.question === '请选择安全提示问题' || this.answer === '';
        }
    },
    data() {
        return {
            openEye: false,
            questionList: [],
            checkedNo: '',
            showList: false,
            newName: '',
            answer: '',
            question: '请选择安全提示问题'
        }
    },
    methods: {
        select(val, index) {
            this.checkedNo = index;
            this.question = val;
        },
        showQuestions() {
            this.showList = !this.showList;
        },
        nextPage() {
            this.pageLoading();
            this.Request('verifyCode', { //验证验证码
                session_id: session_id,
                type: 'username', //注册方式 (mobile 或 email)
                account: this.newName,
                verify_code: '',
                question: this.question,
                answer: this.answer
            }).then(res => {
                this.closePageLoading();
                if (res.error == 0) {
                    this.Cacher.setData('questions', {
                        from: 'questions',
                        info: {
                            type:'username', //验证类型
                            session_id,
                            account: this.newName,
                            registerType, //注册类型
                            question: this.question,
                            answer: this.answer,
                            verify_code:''
                        }
                    })
                    uni.navigateTo({
                        url: './setNew?from=questions'
                    })
                } else {
                    this.Toast(res.message)
                }
            }).catch(res => {
                this.Toast(res.message)
            })
        },
        getName(val) {
            if (val.type == 'input') {
                this.newName = val.detail;
            } else {
                this.newName = '';
            }
        },
        getAnswer(val) {
            if (val.type == 'input') {
                this.answer = val.detail;
            } else {
                this.answer = '';
            }
        }
    },
    onLoad() {
        this.pageLoading();
        this.Request('initPassword', {}).then(res => {
            session_id = res.session_id;
            questions = res.settings.questions;
            registerType = res.settings.type;
            this.questionList=questions;
            this.closePageLoading();
        })
    }
}
